.offers {
  margin-top: 130px !important;
  width: 75%;
  height: 60vh;
  display: flex;
  margin: auto;
  /* padding: 0px 140px; */
  margin-bottom: 150px;
  background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
}

.offers-left {
  flex: 1;
  display: flex;
  padding-left: 10%;
  flex-direction: column;
  justify-content: center;
}

.offers-left h1 {
  color: #171717;
  font-size: 60px;
  font-weight: 600;
}

.offers-left p {
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}

.offers-left button {
  width: 182px;
  height: 70px;
  border-radius: 35px;
  background-color: #ff4141;
  border: none;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
}

.offers-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 50px;
}

.offers-right img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

@media (max-width: 1280px) {
  .offers {
    padding: 0px 80px;
    margin-bottom: 120px;
  }

  .offers-left h1 {
    font-size: 40px;
  }
  .offers-left p {
    font-size: 18px;
  }
  .offers-right img {
    width: 300px;
  }

  .offers-left button {
    width: 220px;
    height: 50px;
    font-size: 18px;
  }
  .offers-right {
    padding-top: 30px;
  }
}

@media (max-width: 1080px) {
  .offers {
    padding: 0px 60px;
    margin-bottom: 80px;
    height: 40vh;
  }

  .offers-left h1 {
    font-size: 30px;
  }
  .offers-left p {
    font-size: 16px;
  }
  .offers-right img {
    width: 200px;
  }

  .offers-left button {
    width: 160px;
    height: 40px;
    font-size: 16px;
  }
  .offers-right {
    padding-top: 10px;
  }
}

@media (max-width: 800px) {
  .offers {
    margin-bottom: 60px;
  }

  .offers-left h1 {
    font-size: 22px;
  }
  .offers-left p {
    font-size: 12px;
  }
  .offers-right img {
    width: 180px;
  }

  .offers-left button {
    width: 140px;
    height: 30px;
    font-size: 12px;
  }
}

@media (max-width: 500px) {
  .offers {
    height: 30vh;
    width: 90vw;
    margin-bottom: 50px;
  }
  .offers-left {
    padding-left: 0;
  }
  .offers-left h1 {
    font-size: 18px;
  }

  .offers-right img {
    width: 130px;
  }

  .offers-left button {
    width: 120px;
    height: 25px;
    font-size: 10px;
    margin-top: 12px;
  }
}
