.popular {
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center !important;
  align-items: center;
  gap: 10px;
}

.popular h1 {
  color: #171717;
  font-size: 50px;
  font-weight: 600;
}

.popular hr {
  width: 200px;
  height: 6px;
  border-radius: 10px;
  background: #252525;
}
.popular-item {
  width: 90%;
  margin-top: 50px;
  padding: 0% 10%;
  display: flex;
  gap: 30px;
}

@media (max-width: 1280px) {
  .popular {
    height: 70vh;
  }

  .popular h1 {
    font-size: 40px;
  }

  .popular hr {
    width: 160px;
    height: 4px;
  }

  .popular-item {
    gap: 30px;
    margin-top: 30px;
  }
}

@media (max-width: 1024px) {
  .popular {
    height: 60vh;
  }

  .popular h1 {
    font-size: 30px;
  }

  .popular hr {
    width: 120px;
    height: 3px;
  }

  .popular-item {
    gap: 20px;
    margin-top: 20px;
  }
}

@media (max-width: 800px) {
  .popular {
    height: 50vh;
    gap: 6px;
  }

  .popular h1 {
    font-size: 20px;
  }

  .popular hr {
    width: 100px;
  }

  .popular-item {
    gap: 10px;
  }
}

@media (max-width: 500px) {
  .popular {
    height: auto;
    /* height: 120vh; */
  }
  .popular-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 25px;
  }
}
