.newsLetter {
  width: 75%;
  height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin: auto;
  padding: 0px 140px;
  margin-bottom: 150px;
  margin-top: 140px;
  background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
}

.newsLetter h1 {
  color: #454545;
  font-size: 45px;
  font-weight: 600;
}
.newsLetter p {
  color: #454545;
  font-size: 20px;
}

.newsLetter div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: white;
  width: 730px;
  height: 70px;
  border-radius: 80px;
  border: 1px solid #e3e3e3;
}

.newsLetter input {
  width: 500px;
  margin-left: 30px;
  border: none;
  outline: none;
  color: #616161;
  font-family: Poppins;
  font-size: 16px;
}

.newsLetter button {
  width: 210px;
  height: 70px;
  border-radius: 80px;
  background: black;
  color: white;
  font-size: 16px;
  cursor: pointer;
}

@media (max-width: 1280px) {
  .newsLetter {
    padding: 0px 80px;
    margin-bottom: 120px;
    gap: 20px;
  }

  .newsLetter h1 {
    font-size: 36px;
  }
  .newsLetter p {
    font-size: 16px;
  }

  .newsLetter div {
    width: 700px;
    height: 60px;
  }
  .newsLetter div input {
    width: 400px;
  }

  .newsLetter div button {
    width: 160px;
    height: 60px;
  }
}

@media (max-width: 1024px) {
  .newsLetter {
    margin-bottom: 60px;
  }

  .newsLetter h1 {
    font-size: 30px;
  }
  .newsLetter p {
    font-size: 14px;
  }

  .newsLetter div {
    width: 500px;
    height: 50px;
  }
  .newsLetter div input {
    width: 300px;
    font-size: 14px;
  }

  .newsLetter div button {
    width: 120px;
    height: 50px;
    font-size: 14px;
  }
}

@media (max-width: 800px) {
  .newsLetter {
    margin-bottom: 30px;
    height: 35vh;
  }

  .newsLetter h1 {
    font-size: 18px;
  }
  .newsLetter p {
    font-size: 12px;
  }

  .newsLetter div {
    width: 400px;
    height: 40px;
  }
  .newsLetter div input {
    width: 200px;
    font-size: 13px;
  }

  .newsLetter div button {
    width: 100px;
    height: 40px;
    font-size: 13px;
  }
}

@media (max-width: 500px) {
  .newsLetter {
    height: 25vh;
    width: 100%;
    padding: 0;
    gap: 15px;
    margin-top: 0;
  }

  .newsLetter h1 {
    font-size: 16px;
  }
  .newsLetter p {
    font-size: 13px;
    max-width: 200px;
    text-align: center;
  }

  .newsLetter div {
    width: 300px;
  }
  .newsLetter div input {
    width: 130px;
  }
}
